			var authorsQTDToShowInsideBracktes = qtdAutores - 2;

			var linkToggleOn = $('<a data-toggle="tooltip" data-placement="top" title="+'+authorsQTDToShowInsideBracktes+'"></a>');

			linkToggleOn.text("[...]");
